import React, { useState, useEffect } from 'react';
import { StyleSheet, Text, View, Image, Button, TouchableOpacity, FlatList, StatusBar } from 'react-native';
import data from './assets/cards.json';
import SmallClassImage from './SmallClassImage';
import SoundButton from './SoundButton';
import Card from './Card';
import FlatListItemSeparator from './FlatListItemSeparator';
import cardsets from './assets/cardsets.json';
import heroes from './assets/heroes.json';
import { NavigationContainer, useScrollToTop, useIsFocused } from '@react-navigation/native';
import { Toolbar } from 'react-native-material-ui';

const CardListTab = ( { route, navigation, state }) => {

  React.useLayoutEffect( () => {
    navigation.setOptions({headerShown: false});
  }, [navigation]);

  const flatListRef = React.useRef()
  useScrollToTop(flatListRef)

  const {cardSet} = route.params
  const {typeFilter} = route.params

  const myData = filterData()
  const heroIDs = heroes.map(a => a.id)

    function filterData() {
      const heroIDs = heroes.map(a => a.id)
      var filteredData;

      if (typeFilter == 'MINION') {
        filteredData = data
          .filter(x => (x.collectible === true && x.type === 'MINION') && (x.set === cardSet || cardSet === null))
      }
      else {
        filteredData = data
          .filter(x => heroIDs.includes(x.id))
      }

      return filteredData
        .sort(function(a,b) {
          var classA = a.cardClass
          var classB = b.cardClass

          if(classA < classB ) { return -1}
          if(classA > classB ) { return 1}
          if(classA < classB ) { return 0}
        })
        .slice(0,100)
    }

    const [cardDisplayed, setCardDisplayed] = useState('none')
    const [cardID, setCardID] = useState('AT_009')
    const [flavorText, setFlavorText] = useState('')
    const [myCardData, setMyCardData] = useState(myData)

    const [cards, setCards] = useState([])

  function search(criteria) {
    setMyCardData(myData.filter(x => (x.name.toUpperCase()).includes(criteria.toUpperCase())))
  }

  navigation.addListener('tabPress', e => {
    setMyCardData(myData)
    navigation.navigate(
      typeFilter == 'MINION' ? 'Cards' : 'Heroes',
       {cardSet: null})

  })

  navigation.addListener('focus', e => {
    goToTop()
    setMyCardData(myData)
  })

  function goToTop() {
    flatListRef.current.scrollToOffset({ animated: true, offset: 0 })
  }

  const styles = StyleSheet.create({
    Button: {
      fontSize: '12px',
      backgroundColor: 'green'
    }
  });

  const openNewCard = (newID) => {
    console.log('Test Parent Method ' + newID)
    var flavorText = findFlavor(newID)
    cards.push([newID, flavorText])
    setCardID(newID)
    setFlavorText(flavorText)
  }

  function findFlavor(cardID) {
    return heroIDs.includes(cardID)
      ? heroes.find(x => x.id == cardID).flavorText
      : data.find(x => x.id == cardID).flavor
  }

  const returnToPreviousCard = () => {
    console.log('Go back to previous card.')
    setCardID(cards[cards.length-2][0])
    setFlavorText(cards[cards.length-2][1])
    cards.pop()
  }

  return (
      <>

      <Toolbar
          centerElement="HearthSFX"
          searchable={{
            autoFocus: true,
            placeholder: 'Search',
            onChangeText: (criteria) => { search(criteria) }
          }}
        />
          <View style={{ flex: 1, padding: 8, flexDirection: 'row'}}>
            <FlatList
              data={myCardData}
              renderItem={({ item }) => (
                <TouchableOpacity onPress= {() => {
                              setCardID(item.id)
                              /* Flavor comes from the heroes.json if we're looking at a Hero */
                              var flavorText = (
                                heroIDs.includes(item.id)
                                ? heroes.find(element => element.id == item.id).flavorText
                                : item.flavor
                              )
                              setFlavorText(flavorText)
                              if (cardDisplayed == 'none') {
                                setCardDisplayed('')
                              }
                              setCards([[item.id, flavorText]])
                            }}>

                  <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
                  , justifyContent: 'space-between'
                  , marginTop: 4, marginBottom: 4, marginRight: 8}}>
                    <SmallClassImage cardClass={item.cardClass}/>
                    <Text style={{flexGrow: 1}} >{item.name}</Text>
                    {typeFilter == 'MINION' ?
                      <>
                      <SoundButton title='Play' cardID={item.id} sound='P' />
                      <View style={{padding: 4}} />
                      <SoundButton title='Attack' cardID={item.id} sound='A' />
                      <View style={{padding: 4}} />
                      <SoundButton title='Death' cardID={item.id} sound='D' />
                      </>
                      : <></>
                  }
                  </View>
                  </TouchableOpacity>

              )}
              ItemSeparatorComponent={FlatListItemSeparator}
              ref={flatListRef}
            />
            <View style={{justifyContent:'center', display:cardDisplayed}}>
               <Card cardID={cardID} flavorText={flavorText}
                  openNewCard={openNewCard}
                  returnToPreviousCard={returnToPreviousCard}
                  previousCardExists={(cards.length>1) ? true : false}/>
            </View>
          </View>
          </>
        )
}

export default CardListTab;
