import React from 'react';
import {Image} from 'react-native';

const SmallClassImage = (props) => {
  const cardClass = props.cardClass
  var img = 'class_small_neutral'

  function cardImage() {
    switch (cardClass) {
      case 'DEMONHUNTER':
        return require('./assets/img/class_small_demonhunter.png')
        break;
      case 'DRUID':
        return require('./assets/img/class_small_druid.png')
        break;
      case 'HUNTER':
        return require('./assets/img/class_small_hunter.png')
        break;
      case 'MAGE':
        return require('./assets/img/class_small_mage.png')
        break;
      case 'PALADIN':
        return require('./assets/img/class_small_paladin.png')
        break;
      case 'PRIEST':
        return require('./assets/img/class_small_priest.png')
        break;
      case 'ROGUE':
        return require('./assets/img/class_small_rogue.png')
        break;
      case 'SHAMAN':
        return require('./assets/img/class_small_shaman.png')
        break;
      case 'WARLOCK':
        return require('./assets/img/class_small_warlock.png')
        break
      case 'WARRIOR':
        return require('./assets/img/class_small_warrior.png')
        break;
      default:
        return require('./assets/img/class_small_neutral.png')
    }
  }

  return (
    <Image style={{width: 34, height: 34}} source={cardImage()}/>
  )
}

export default SmallClassImage;
