import React from 'react';
import MinionDefaultSounds from './MinionDefaultSounds';
import HeroDefaultSounds from './HeroDefaultSounds';
import TokensFlatList from './TokensFlatList';
import SpecialsFlatList from './SpecialsFlatList';
import { Text, View, Image, ScrollView, Button, TouchableOpacity } from 'react-native';
import heroes from './assets/heroes.json';
import tokens from './assets/tokens.json';
import { Ionicons } from '@expo/vector-icons';

const Card = (props) => {

  const heroIDs = heroes.map(a => a.id)
  const isHero = heroIDs.includes(props.cardID)
  const imgSrc = (isHero ?
    'https://hearthstonesounds.s3.amazonaws.com/' :
    'https://art.hearthstonejson.com/v1/render/latest/enUS/256x/')
     + props.cardID + '.png'

  const openNewCard = (newID) => {
    console.log('Test Card Method ' + newID)
    props.openNewCard(newID)
  }

  return (
    <ScrollView style={{width: 512}}>
      {props.previousCardExists ?
        <TouchableOpacity onPress={() => {props.returnToPreviousCard()}}>
          <Ionicons name="md-arrow-round-back" size={36} color="#bdbdbd" />
        </TouchableOpacity>
        : <></>
      }

      <View style={{ flex: 1, padding: 8, alignItems: 'center'}}>
        {isHero
          ? <Image source={{uri: imgSrc}} style={{width: 250, height: 345}}/>
          : <Image source={{uri: imgSrc}} style={{width: 256, height: 388}}/>
        }
        <Text style={{width: 512, padding: 16}}>{props.flavorText}</Text>
        <Divider/>
        {isHero
          ? <HeroDefaultSounds cardID={props.cardID}/>
          : <MinionDefaultSounds cardID={props.cardID}/>
        }
        <TokensFlatList cardID={props.cardID} openNewCard={openNewCard}/>
        <Divider/>
        <SpecialsFlatList cardID={props.cardID} openNewCard={openNewCard}/>
      </View>
    </ScrollView>
  )
}

const Divider = () => {
  return (
    <View
      style={{
        height: 1,
        width: "100%",
        opacity: 0.25,
        backgroundColor: "#000000",
        marginRight: 8,
        marginBottom: 16
      }}
    />
  )
}

export default Card
